.App {
  text-align: center;
  background-color: green;
  background-image: linear-gradient(green, rgb(0, 65, 0));

  height: 100vh;
  width: 100vw;
  padding-top: 25px;
}
.displaySection{
  height: 95vh;
  width: 90vw;
  min-width: 800px;
  background-color: rgb(221, 221, 221);
  border-radius: 5px;
  margin-left: 4vw;
  padding: 10px;
  position: relative;
  box-shadow: 0 0 15px rgb(68, 68, 68);
}
.table{
  background-color: gray;
  border-radius: 5px;  
  width: 45%;
  padding:10px;
  text-align: center;
  display: inline-block;
  position: relative;
  margin-right: 20px;
  margin-bottom: 20px;
  box-shadow: 0 0 10px rgb(105, 105, 105);
  border: 1px solid black;
}
.title{
  width: 100%;
  background-color: rgba(0,0,0,0);
  outline: none;
  border: none;
  font-size: 25px;  
  text-align: center;
}
.titleSmaller{
  color: rgb(39, 39, 39);
  font-size: 20px;
}
.column{
  height: 320px;
  width: 33%;  
  text-align: center;  
  display: inline-block;
}
.column textarea{
  width: 85%;
  height: 100%;  
  outline: none;
  border: none;
  display: inline-block;  
  background-color: rgba(224, 224, 224, 0.5);
  padding: 10px;  
}
.addButton{
  height: 40PX;
  width: 40px;
  border-radius: 50px;
  background-color: green;
  color: white;
  font-size: 40px;
  line-height: 30px;
  text-align: center;
  user-select: none;
  margin: 5px;
  margin-left: -8px;
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 2;
  cursor: pointer;
}
.addButton:hover{  
  background-color: rgb(106, 184, 106);  
}
.hoverMenuButton{
  height: 25px;
  width: 25px;
  border-radius: 20px;
  background-color: rgb(107, 107, 107);
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
.hoverMenuButton .hoverMenu{
  display: none;
  height: 200px;
  width: 200px;
  background-color: gray;
  border: 1px solid black;
  z-index: 2;
}
.hoverMenuButton:hover .hoverMenu{
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  background-color: gray;
  border-radius: 5px;
}
.hoverMenuButtonInner{
  width: 100%;
  border-bottom: 1px solid black;
  user-select: none;
}
.hoverMenuButtonInner:hover{
  background-color: rgb(192, 192, 192);
}